<script>
import G6 from "@antv/g6";
import insertCss from 'insert-css';
import {
  uuid
} from 'vue-uuid';
import marked from 'marked';
import 'github-markdown-css/github-markdown.css'

export default {
  name: "workflowEdit",
  data() {
    return {
      app_title: "",
      visible_md: false,
      html: "",
      uuid: "",
      baseURL: this.BaseURL,
      app_list: [],
      graph: "",
      childNode: "",
      data: {
        nodes: [],
        edges: []
      },
      app_data: {},
      node_color: "",
      edge_color: "",
      plug_minimap: null,
      plug_toolbar: null,
      plug_contextMenu: null,
      visible_edit: false,
      visible_log: false,
      visible_edit_edge: false,
      form: this.$form.createForm(this),
      form_edge: [],
      key: null,
      app_info: {},
      app_info_args: [],
      workflow_name: "",
      is_mounted: 0,
      start_app: "",
      end_app: "",
      websock: null,
      ping_timetimer: null,
      execute_log: [],
      select_type: "",
      type_data: [],
      test_button_disabled: false,
      test_result: '',
      test_button_loading: false,
      if_order: [
        {key: 1, name: '等于'},
        {key: 2, name: '小于'},
        {key: 3, name: '大于'},
        {key: 4, name: '小于等于'},
        {key: 5, name: '大于等于'},
        {key: 6, name: '包含(区分大小写)'},
        {key: 7, name: '包含(不区分大小写)'},
        {key: 8, name: '长度等于'},
        {key: 9, name: '长度小于'},
        {key: 10, name: '长度大于'},
        {key: 11, name: '长度大于等于'},
        {key: 12, name: '长度小于等于'},
      ]
    };
  },
  created() {
    document.body.ondrop = function (event) {
      event.preventDefault(); //阻止dom默认事件，比如 表单的submit
      event.stopPropagation(); //阻止事件冒泡
    };
  },
  mounted() {
    this.is_mounted++;
    this.uuid = this.$route.params.uuid;
    this.$store.commit("closeCollapsed");
    this.onLoad();
  },

  methods: {
    showChange(e) {
      console.log(e.value)
    },
    onLoad() {
      this.onColor();
      this.onAppList();
      this.onSelectType();
      this.onData();


      // this.init_start_app('/app/basic/start.jpg','开始','start',50,100);
      // this.init_start_app('/app/basic/end.jpg','结束','end',500,100);
    },
    onShowMd() {
      let app_info = this.app_data[this.key];

      let md_url = "/app/" + app_info["app_dir"] + "/readme.md";

      this.$http
        .get(md_url)
        .then((res) => {
          this.app_title = app_info["name"] + " v" + app_info["version"];

          marked.setOptions({
            gfm: true,
            tables: true,
            breaks: true,
            smartLists: true
          });

          this.html = marked(res);
          this.visible_md = true;
        });
    },
    onCloseMd() {
      this.visible_md = false;
    },
    onSelect(value) {
      this.select_type = value;
      this.onSave();
    },
    onSelectType() {
      this.$http
        .post("/api/v1/get/type/list", {
          type: 1,
        })
        .then((res) => {
          if (res.code == 0) {
            this.type_data = res.data;
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onSave() {
      this.data = this.graph.save();
      // 更新剧本信息
      this.$http
        .post("/api/v1/post/workflow/update", {
          uuid: this.uuid,
          name: this.workflow_name,
          start_app: this.start_app,
          end_app: this.end_app,
          flow_json: JSON.stringify(this.data),
          flow_data: JSON.stringify(this.app_data),
          type_id: this.select_type
        })
        .then((res) => {
          if (res.code == 0) {
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onRun() {
      this.data = this.graph.save();
      let uuid_list = new Set()
      for (let i in this.data.edges) {
        uuid_list.add(this.data.edges[i].source)
        uuid_list.add(this.data.edges[i].target)
      }
      for (let i of uuid_list) {
        // console.log(this.app_data[i]);
        if (typeof this.app_data[i] != "undefined") {
          if (this.app_data[i].hasOwnProperty("data") == false) {
            this.$message.warning("请选择 APP 动作");
            return false;
          }
        }
      }


      this.onShowLog();
      this.initWebSocket();
      // 更新剧本信息
      this.$http
        .post("/api/v1/post/workflow/update", {
          uuid: this.uuid,
          name: this.workflow_name,
          start_app: this.start_app,
          end_app: this.end_app,
          flow_json: JSON.stringify(this.data),
          flow_data: JSON.stringify(this.app_data),
          type_id: this.select_type
        })
        .then((res) => {
          if (res.code == 0) {
            let msg = JSON.stringify({
              "method": "run",
              "data": {
                "uuid": this.uuid
              }
            })

            this.sendMSG(msg)
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onData() {
      // 加载剧本信息
      this.$http
        .post("/api/v1/post/workflow/detail", {
          uuid: this.uuid
        })
        .then((res) => {
          if (res.code == 0) {
            this.select_type = String(res.data.type_id);
            this.workflow_name = res.data.name;

            if (res.data.flow_json === "") {
              this.data = {
                nodes: [],
                edges: []
              }
            } else {
              this.data = JSON.parse(res.data.flow_json);
            }

            if (res.data.flow_data === "") {
              this.app_data = {}
            } else {
              this.app_data = JSON.parse(res.data.flow_data);
            }

            this.start_app = res.data.start_app;
            this.end_app = res.data.end_app;

            this.onFlowPlug();
            this.onFlowChart();
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onAppList() {
      // 加载 APP 列表
      this.$http
        .get("/api/v1/get/app/list")
        .then((res) => {
          if (res.code == 0) {
            this.app_list = res.data;
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onThemeCss() {
      insertCss(`
                .g6-minimap-container {
                    border: 1px solid #606067;
                }

                .g6-minimap-viewport {
                    border: 2px solid #000;
                }

                .g6-minimap {
                    position: fixed;
                    right: 10px;
                    bottom: 10px;
                }

                .g6-component-toolbar li {
                    list-style-type: none !important;
                }

                .g6-component-contextmenu {
                    position: absolute;
                    list-style-type: none;
                    padding: 10px 8px;
                    left: -150px;
                    background-color: rgba(255, 255, 255, 0.9);
                    border: 1px solid #e2e2e2;
                    border-radius: 4px;
                    font-size: 12px;
                    color: #3c3c3c;
                }

                .g6-component-contextmenu ul {
                    padding-left: 0px;
                    line-height: 30px;
                    margin: 0px;
                }

                .g6-component-contextmenu li {
                    cursor: pointer;
                    list-style-type:none;
                    list-style: none;
                    margin-left: 0px;
                }

                .g6-component-contextmenu li:hover {
                    color: #a76f36;
                }

                .g6-component-toolbar {
                    position: absolute;
                    list-style-type: none;
                    padding: 6px;
                    left: 10px;
                    top: inherit;
                    bottom: 10px;
                    background-color: rgba(255, 255, 255, 0.9);
                    border: 1px solid #e2e2e2;
                    border-radius: 4px;
                    font-size: 12px;
                    color: #545454;
                    margin: 0;
                }
            `);
    },
    onColor() {
      var theme = this.$store.getters.getTheme;

      if (theme == "blue" || theme == "dark1") {
        this.node_color = "#000000";
        this.edge_color = "#000000";
      } else if (theme == "dark") {
        this.node_color = "#FFFFFF";
        this.edge_color = "#F6BD16";
      }
    },
    onFlowPlug() {
      this.onThemeCss();

      // 小地图
      this.plug_minimap = new G6.Minimap({
        size: [150, 100],
      });

      // 工具
      this.plug_toolbar = new G6.ToolBar();

      // 右键菜单
      this.plug_contextMenu = new G6.Menu({
        getContent(graph) {
          return `<ul>
                <li>编辑</li>
                <li>删除</li>
                </ul>`;
        },
        shouldBegin: (evt) => {
          const item = evt.item
          const type = item._cfg.type;
          if (type === 'node') {
            if (item._cfg.id !== this.start_app && item._cfg.id !== this.end_app) {
              return true
            } else {
              return false
            }
          } else if (type === 'edge') {
            return true
            // if (item._cfg.source._cfg.id !== this.start_app && item._cfg.source._cfg.id !== this.end_app) {
            //   return true
            // } else {
            //   return false
            // }
          }

        },
        handleMenuClick: (target, item) => {
          let action = target.innerHTML;

          let type = item._cfg.type;
          let name = item._cfg.model.label;
          let id = item._cfg.id;


          if (action == "编辑") {
            if (id == this.start_app) {
              this.$message.warning("开始节点不可使用编辑功能");
              return false;
            } else if (id == this.end_app) {
              this.$message.warning("结束节点不可使用编辑功能");
              return false;
            }
            // console.log('编辑node:', id)
            this.onShow(id, name, type);
          } else if (action == "删除") {
            if (id == this.start_app) {
              this.$message.warning("开始节点不能删除");
              return false;
            } else if (id == this.end_app) {
              this.$message.warning("结束节点不能删除");
              return false;
            } else {
              this.$delete(this.app_data, id);
              this.graph.removeItem(item);
              this.onSave();
            }
          }

        },
        // 需要加上父级容器的 padding-left 16 与自身偏移量 10
        offsetX: 0,
        // 需要加上父级容器的 padding-top 24 、画布兄弟元素高度、与自身偏移量 10
        offsetY: 0,
        // 在哪些类型的元素上响应
        itemTypes: ["node", "edge"],
      });
    },
    onDragendAddNode(event, icon, name, app_dir) {
      // 坐标转换
      // console.log(event);

      let point = this.graph.getPointByClient(event.screenX, event.screenY)

      let node_id = uuid.v1();

      if (name == "开始") {
        if (this.start_app != "") {
          this.$message.warning("只能存在一个开始节点");
          return false;
        }
        this.start_app = node_id;
      } else if (name == "结束") {
        if (this.end_app != "") {
          this.$message.warning("只能存在一个结束节点");
          return false;
        }
        this.end_app = node_id;
      }

      let new_node = {
        id: node_id,
        label: name,
        img: icon,
        x: point.x,
        y: point.y
      };

      this.graph.addItem('node', new_node);

      this.data.nodes = this.graph.save().nodes;
      this.app_data[node_id] = this.app_list[app_dir]

      this.onTheme();
      this.onSave();
    },

    init_start_app(icon, name, app_dir, x, y) {

      let node_id = uuid.v1();

      if (name == "开始") {
        if (this.start_app != "") {
          return false;
        }
        this.start_app = node_id;
      } else if (name == "结束") {
        if (this.end_app != "") {
          return false;
        }
        this.end_app = node_id;
      }

      let new_node = {
        id: node_id,
        label: name,
        img: icon,
        x: x,
        y: y
      };

      this.graph.addItem('node', new_node);

      this.data.nodes = this.graph.save().nodes;

      this.app_data[node_id] = this.app_list[app_dir]

      this.onTheme();
      this.onSave();
    },

    onFlowChart() {
      // 初始化流程图
      const width = document.getElementById("container").scrollWidth;
      const height = document.getElementById("container").scrollHeight;

      this.graph = new G6.Graph({
        container: "container",
        width: width,
        height: height,
        fitCenter: true,
        defaultNode: {
          type: "image",
          size: 60,
          clipCfg: {
            show: true,
            type: 'circle',
            r: 27
          },
          labelCfg: {
            style: {
              fill: this.node_color,
            },
          },
          anchorPoints: [
            [0, 0.25],
            [0, 0.5],
            [0, 0.75],
            [1, 0.25],
            [1, 0.5],
            [1, 0.75],
            [0.25, 0],
            [0.5, 0],
            [0.75, 0],
            [0.25, 1],
            [0.5, 1],
            [0.75, 1]
          ],
        },
        defaultEdge: {
          type: "cubic-horizontal",
          labelCfg: {
            autoRotate: true,
            refY: 10,
            refX: 0,
            style: {
              fill: this.node_color
            }
          },
          style: {
            endArrow: {
              path: G6.Arrow.triangle(),
            },
            stroke: this.edge_color,
            lineAppendWidth: 40
          },
        },
        modes: {
          default: [
            {type: 'drag-canvas',},
            {type: 'drag-node',},
            {
              type: 'zoom-canvas',
              sensitivity: 0.5,
            },
            {type: 'click-select',},
            {type: 'brush-select',},

          ],
          edit: [
            {
              type: 'create-edge',
              trigger: 'drag',
            },
          ],
        },
        plugins: [this.plug_minimap, this.plug_toolbar, this.plug_contextMenu],
        enabledStack: true,
      });

      this.graph.read(this.data);

      this.is_mounted++;

      this.onTheme();
      this.onFlowChartEvent();

      // 默认添加开始结束节点
      this.init_start_app(this.baseURL + '/app/basic/start.jpg', '开始', 'start', 50, 100);
      this.init_start_app(this.baseURL + '/app/basic/end.jpg', '结束', 'end', 500, 100);
    },
    onFlowChartEvent() {
      // 初始化流程图事件

      this.graph.on("node:mouseout", (evt) => {
        // 鼠标移出节点，切换 default 模式，可拖拽节点和画布等操作
        this.graph.setMode('default');
      });

      this.graph.on("node:dblclick", (evt) => {
        // const item = evt.item; // 被操作的节点 item
        // const target = evt.target; // 被操作的具体图形
        // 鼠标双击节点，切换 edit 模式，可进行连线
        this.graph.setMode('edit');
      });

      this.graph.on("node:click", (evt) => {
        // const item = evt.item; // 被操作的节点 item
        // const target = evt.target; // 被操作的具体图形
        // 鼠标双击节点，切换 edit 模式，可进行连线
        // console.log(item.get('id'))

        this.graph.setMode('edit');
      });

      this.graph.on("dragend", (evt) => {
        this.data = this.graph.save();
        this.onSave();
      });

      // this.graph.on('beforecreateedge', e => {
      //     console.log(this.graph.save())
      //     console.log(JSON.stringify(this.data));
      // })

      this.graph.on('aftercreateedge', e => {
        let edge_id = uuid.v1();

        this.graph.updateItem(e.edge, {
          id: edge_id,
          label: '',
        });

        this.onTheme();
        this.onSave();
      })
    },
    onTheme() {
      for (let index = 0; index < this.data.nodes.length; index++) {
        let node_id = this.data.nodes[index].id;
        let item = this.graph.findById(node_id);

        this.graph.updateItem(item, {
          labelCfg: {
            style: {
              fill: this.node_color,
            }
          },
        });
      }

      for (let index = 0; index < this.data.edges.length; index++) {
        let edge_id = this.data.edges[index].id;
        let item = this.graph.findById(edge_id);
        if (edge_id.indexOf("edge") != -1) {
          this.graph.removeItem(item)
        } else if (item !== undefined) {
          this.graph.updateItem(item, {
            style: {
              stroke: this.edge_color
            },
            labelCfg: {
              style: {
                fill: this.node_color
              }
            }
          });
        }
      }

      this.onSave();
    },
    onShow(key, name, type) {
      this.key = key;
      if (type === "node") {
        this.form.setFieldsValue({})
        this.app_info = this.app_data[key];
        var edit_data = this.app_data[key]["data"];
        if (typeof edit_data != "undefined") {

          edit_data["node_name"] = name;
          this.selectAppAction(edit_data.action)
          setTimeout(() => {
            this.form.setFieldsValue(edit_data);
            this.argsCheck();
          }, 100);
        } else {
          this.app_info_args = [];
          setTimeout(() => {
            this.form.setFieldsValue({
              "node_name": name,
              "action": ""
            });
            this.argsCheck();
          }, 100);
        }
        this.visible_edit = true;

      } else if (type === "edge") {
        // setTimeout(() => {
        //   this.form_edge.setFieldsValue({
        //     "ifelse": name
        //   });
        // }, 100);
        this.visible_edit_edge = true;

      }
    },
    onClose() {
      this.visible_edit = false;
    },
    onCloseEdge() {
      this.visible_edit_edge = false;
    },
    onShowLog() {
      this.visible_log = true;
    },
    onCloseLog() {
      this.execute_log = [];
      this.visible_log = false;
    },
    selectAppAction(action) {
      this.app_info_args = this.app_info["args"][action];
      console.log('app_info_args', this.app_info_args)
    },
    onUpdate(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {

          // 必须用json转换出一个新的对象，才能不是新的地址，如果直接更新，会把object对象下其他的的没有'data'属性的对象也添加data
          // 比如 this.app_data[this.key]['data'] = values;

          let item_data = JSON.parse(JSON.stringify(this.app_data[this.key]))
          item_data['data'] = values
          this.$set(this.app_data, this.key, item_data)

          // console.log('after save', JSON.stringify(this.app_data))
          const item = this.graph.findById(this.key)
          // console.log('update_item',item)
          this.graph.updateItem(item, {
            "label": values.node_name
          });
          this.onSave();
          this.onClose();

        } else {
          this.$message.warning("form error", this.form.getFieldsValue());
          console.log('form error', this.form.getFieldsValue())
        }
      });
    },
    onUpdateEdge(e) {
      e.preventDefault(); //阻止form的默认提交动作
      this.form_edge.validateFields((err, values) => {
        if (!err) {
          this.graph.updateItem(this.graph.findById(this.key), {
            "id": this.key
          });

          this.onSave();
          this.onCloseEdge();
        }
      });
    },
    initWebSocket() {
      //初始化weosocket
      var wsuri = this.BaseURL.replace("http", "ws").replace("https", "wss") + "/logs";
      // var wsuri = "ws://127.0.0.1:8081/logs";

      this.websock = new WebSocket(wsuri);

      // 连接成功
      this.websock.onopen = this.websocketOpen;

      // 连接异常
      this.websock.onerror = this.websocketError;

      // 接收消息
      this.websock.onmessage = this.websocketMSG;

      // 断开连接
      this.websock.onclose = this.websocketClose;
    },
    websocketPing() {
      this.ping_timetimer = setInterval(() => {

        let msg = JSON.stringify({
          "method": "ping"
        })

        this.sendMSG(msg);
      }, 30000);
    },
    websocketOpen() {
      this.websocketPing();
      //连接建立之后执行send方法发送数据

      let msg = JSON.stringify({
        "method": "ping"
      })

      this.sendMSG(msg);
    },
    websocketError() {
      //连接建立失败重连
      // this.initWebSocket();
      console.log("已经关闭了长连接");

    },
    websocketMSG(e) {
      var result = JSON.parse(e.data);

      if (result.method == "execute_log") {
        var data = result.data;
        this.execute_log.push(data);
      } else {
        this.$message.destroy();
        this.$message.error("长连接未知错误");
      }
    },
    websocketClose(e) {
      //关闭
      // this.initWebSocket();

      console.log("已经关闭了长连接");
    },
    sendMSG(data) {
      //数据发送
      this.websock.send(data);
    },
    argsCheck() {

      this.test_result = '';
      const formItem = this.form.getFieldsValue();
      this.test_button_disabled = false;
      for (let key in formItem) {
        // console.log('key', key)
        let value = formItem[key];
        if (value !== undefined) {
          let start = value.substr(0, 2)
          let end = value.substr(value.length - 2)
          if (value === '' || start === '{{' || end === '}}') {
            this.test_button_disabled = true;
          }
        }
      }
    },
    appRunTest() {
      this.form.validateFields((err, values) => {
        if (!err) {
          this.app_data[this.key]["data"] = values;
          this.graph.updateItem(this.graph.findById(this.key), {
            "label": values.node_name
          });

          this.onSave();
        }
      })
      this.test_button_loading = true
      this.$http
        .post("/api/v1/app/run", {
          key: this.key,
          uuid: this.uuid,
        })
        .then((res) => {
          if (res.code == 0) {
            // console.log('res.data', JSON.stringify(res.data))
            this.test_result = JSON.stringify(res.data);

          } else {
            this.$message.error(res.msg);
          }
          this.test_button_loading = false;

        });
    },

  },
  watch: {
    '$store.getters.getTheme': function (e) {
      if (this.is_mounted > 1) {
        this.onColor();
        this.onTheme();
      }
      // try {
      //     this.graph.destroy()
      // } catch (error) {
      //     console.log(error)
      // }

      // this.onFlowChart(1);
    },
    workflow_name(newValue, oldValue) {
      this.onSave();

    },
  },


};
</script>

