<template>
  <a-layout-content>
    <div class="header_div">
      <a-select default-value="0" style="width: 120px" @change="onSelect" class="align">
        <a-select-option value="0">全部</a-select-option>
        <a-select-option v-for="(item, index) in type_data" :key="index" :value="item.id">{{
            item.name
          }}
        </a-select-option>
      </a-select>
      <a-input-search placeholder="请输入剧本名称" style="width: 200px" @search="onSearch" class="align"/>
      <a-button type="primary" icon="plus-circle" class="align btn_add" @click="tzAddPlayBook">
        Playbook
      </a-button>
    </div>
    <a-table rowKey="id" :columns="columns" :data-source="data" :loading="loading">
      <a-tag slot="type_name" slot-scope="text" class="color1">
        {{ text }}
      </a-tag>

      <span slot="name" slot-scope="text">
            <b>{{ text }}</b>
        </span>

      <span slot="update_time" slot-scope="text">
            {{ text.replace("T", " ") }}
        </span>

      <span slot="create_time" slot-scope="text">
            {{ text.replace("T", " ") }}
        </span>

      <span slot="action" slot-scope="text, record">
        <a-tag color="#1ABC9C"  @click="onRun(record.uuid)"><a-icon type="play-square" /></a-tag>

            <a-popconfirm title="是否要删除该剧本?" ok-text="是" cancel-text="否" @confirm="del(record.uuid)">
                <a-tag color="#e45f5f" class="pointer"> 删除 </a-tag>
            </a-popconfirm>

            <a-tag color="#2db7f5" class="pointer" @click="tzUpdatePlayBook(record.uuid)">
                编辑
            </a-tag>



        </span>
    </a-table>
    <a-drawer class="visible_log" :closable="false" :width="500" :maskClosable="false" :visible="visible_log"
              @close="onCloseLog">
      <div>
        <a-icon class="pointer" type="close-circle" @click="onCloseLog"/>
      </div>
      <pre class="log_div">
            <p v-for="(item,index) in execute_log" :key="index"><span
              class="appname">[{{ item.app_name }}]</span> - <span class="appresult">{{ item.result }}</span> - <span
              class="time">{{ item.create_time.replace("T", " ") }}</span></p>
        </pre>
    </a-drawer>
  </a-layout-content>

</template>

<script>
const columns = [
  {
    title: "分类",
    dataIndex: "type_name",
    key: "type_name",
    scopedSlots: {
      customRender: "type_name",
    },
  }, {
    title: "剧本名称",
    dataIndex: "name",
    key: "name",
    scopedSlots: {
      customRender: "name",
    },
  },
  {
    title: "创建人",
    dataIndex: "nick_name",
    key: "nick_name",
  },
  {
    title: "更新时间",
    key: "update_time",
    dataIndex: "update_time",
    scopedSlots: {
      customRender: "update_time",
    },
  },
  {
    title: "创建时间",
    key: "create_time",
    dataIndex: "create_time",
    scopedSlots: {
      customRender: "create_time",
    },
  },
  {
    title: "操作",
    key: "action",
    scopedSlots: {
      customRender: "action",
    },
    width: 200,
  }
];

export default {
  name: "workflowHome",
  data() {
    return {
      columns,
      loading: false,
      data: [],
      so_text: "",
      select_type: "0",
      type_data: [],
      execute_log: [],
      visible_log: false,
      websock:null,

    };
  },
  mounted() {
    this.onLoad();
    this.initWebSocket();

  },
  methods: {
    onLoad(keywords = "", type = "0") {
      this.loading = true;
      this.onSelectType();
      this.$http
        .post("/api/v1/get/workflow/list", {
          keywords: keywords,
          type: type
        })
        .then((res) => {
          if (res.code == 0) {
            this.data = res.data;
            this.loading = false;
          } else {
            this.$message.error(res.msg);
            this.loading = false;
          }
        });
    },
    onSelectType() {
      this.$http
        .post("/api/v1/get/type/list", {
          type: 1,
        })
        .then((res) => {
          if (res.code == 0) {
            this.type_data = res.data;
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    del(uuid) {
      this.$http
        .post("/api/v1/post/workflow/del", {
          uuid: uuid,
        })
        .then((res) => {
          if (res.code == 0) {
            this.$message.success("删除成功");
            this.onLoad();
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onSearch(value) {
      this.so_text = value;
      this.onLoad(this.so_text, this.select_type);
    },
    onSelect(value) {
      this.select_type = value;
      this.onLoad(this.so_text, this.select_type);
    },
    tzAddPlayBook() {
      this.$http
        .post("/api/v1/post/workflow/add")
        .then((res) => {
          if (res.code == 0) {
            let uuid = res.data.uuid;
            this.$router.push({
              name: "WorkflowEdit",
              params: {
                uuid: uuid
              }
            });
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    tzUpdatePlayBook(uuid) {
      this.$router.push({
        name: "WorkflowEdit",
        params: {
          uuid: uuid
        }
      });
    },
    initWebSocket() {
      //初始化weosocket
      var wsuri = this.BaseURL.replace("http", "ws").replace("https", "wss") + "/logs";
      // var wsuri = "ws://127.0.0.1:8081/logs";

      this.websock = new WebSocket(wsuri);

      // 连接成功
      this.websock.onopen = this.websocketOpen;

      // 连接异常
      this.websock.onerror = this.websocketError;

      // 接收消息
      this.websock.onmessage = this.websocketMSG;

      // 断开连接
      this.websock.onclose = this.websocketClose;


    },
    websocketPing() {
      this.ping_timetimer = setInterval(() => {

        let msg = JSON.stringify({
          "method": "ping"
        })

        this.sendMSG(msg);
      }, 30000);
    },
    websocketOpen() {
      this.websocketPing();
      //连接建立之后执行send方法发送数据

      let msg = JSON.stringify({
        "method": "ping"
      })

      this.sendMSG(msg);
    },
    websocketError() {
      //连接建立失败重连
      // this.initWebSocket();
      console.log("已经关闭了长连接");

    },

    websocketMSG(e) {
      var result = JSON.parse(e.data);

      if (result.method == "execute_log") {
        var data = result.data;
        this.execute_log.push(data);
      } else {
        this.$message.destroy();
        this.$message.error("长连接未知错误");
      }
    },
    websocketClose(e) {
      //关闭
      // this.initWebSocket();

      console.log("已经关闭了长连接");
    },
    sendMSG(data) {
      //数据发送
      this.websock.send(data);
    },
    onCloseLog() {
      this.execute_log = [];
      this.visible_log = false;
    },
    onRun(uuid){
      // this.initWebSocket();
      this.visible_log = true;
      // this.initWebSocket();
      let msg = JSON.stringify({
        "method": "run",
        "data": {
          "uuid": uuid
        }
      })
      this.sendMSG(msg)


    }
  },
};
</script>

<style lang="less" scoped>
@import url("../../theme/style.less");

.visible_log {
  /deep/ .ant-drawer-body {
    background: #151719;
    color: #bbb;
    height: 100%;
  }
}

.pointer {
  cursor: pointer;
}

.center {
  text-align: center;
}

.blue,
.dark1 {
  .header_div {
    width: 100%;
    background: #fafafa;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #f5f5f5;
    border-radius: 5px;

    .align {
      vertical-align: middle;
    }

    .btn_add {
      float: right;
    }
  }
}

.blue {
  .color1 {
    background: #03396d;
    color: #fff;
    border: 1px solid #03396d;
  }
}

.dark1 {
  .color1 {
    background: #bd854d;
    color: #fff;
    border: 1px solid #bd854d;
  }
}

.dark {
  .header_div {
    width: 100%;
    background: #202225;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #353535;
    border-radius: 5px;

    .align {
      vertical-align: middle;
    }

    .btn_add {
      float: right;
    }
  }

  .color1 {
    background: #bd854d;
    color: #fff;
    border: 1px solid #bd854d;
  }
}
</style>



